export default{
	data(){
		return{
			newActive:"",
			newTransition:true,
			/*用户输入信息*/
			inputInfo:{
				commentContent:''
			}
		};
	},
	props:{
		/*底部弹窗的属性*/
		active:{
			type:Boolean,
			default:false
		},
		height:{
			type:[String],
			default:"100%"
		},
		//遮盖层显示
		maskShow:{
			type:Boolean,
			default:true
		},
		//遮盖层点击
		maskClick:{
			type:Boolean,
			default:true
		}
	},
	computed:{
		popuplayoutClass:function(){
			let _class="";
			if(this.newActive){
				_class+="popup-layout-active";
			}	
			_class+=" popup-layout-bottom";
			return _class;
		},
		popupContentClass:function () {
			let _class = "";
			if (this.newTransition&&this.transition!=='none') {
				_class+="popup-layout-transition-slider"
			}
			return _class;
		}
	},
	methods: {
		//显示弹窗
		show:function () {
			this.newActive = true;
			let _this = this;
			setTimeout(function () {
				_this.newTransition = false;
			},50)
		},
		
		//关闭弹窗
		close:function (v) {
			let close = v || true;
			if (close) {
				this.newTransition = true;
				let _this=this;
				setTimeout(function () {
					_this.newActive = false;
					
				},300)
			}
		},
		//取消按钮
		btn_cancel:function(){
			this.close();
	
			//全部置为空
			this.inputInfo={
				commentContent:''
			}
		},
		//确定按钮
		btn_confirm:function(){
			
			if(this.inputInfo.commentContent === ''){
				uni.showModal({
				    content: '请输入评论内容！',
				    showCancel: false
				});
				return;
			} 
	
			this.close();
			
			//调用请求
			this.$emit('commentConfirmCallback',{
				inputInfo:this.inputInfo
			});
		},
	},
}
